import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';

import { Http } from '@angular/http';
import 'rxjs/add/operator/map';
import { AppConfig } from '../../app/app.config';
/**
 * Generated class for the TaskPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
    selector: 'page-task',
    templateUrl: 'task.html',
})
export class TaskPage {
    url: string = AppConfig.getUrl() + "/task/page?pageSize=10&partyNo=" + localStorage.partyNo + "&userId=" + localStorage.userid + "&positionId=" + localStorage.positionId + "&pageNumber=";
    task: any = [];
    pageNo = 0;
    constructor(public navCtrl: NavController, public navParams: NavParams, public http: Http, public alertCtrl: AlertController, public loadingCtrl: LoadingController) {
        this.get()
    }

    ionViewDidLoad() {
        console.log('ionViewDidLoad TaskPage');
    }
    get(event?) {
        if (event) {
            this.pageNo++;
        } else {
            this.pageNo = 0;
            this.task = [];
        }
        let loading = this.loadingCtrl.create({
            content: '加载中...'
        });
        loading.present();
        return new Promise((resolve, reject) => {
            this.http.get(this.url + this.pageNo)
                .map(res => res.json())
                .subscribe(data => {
                    if (event)
                        event.complete();
                    resolve(data);
                    for (let i = 0; i < data.data.length; i++) {
                        var type3;
                        if (data.data[i].type3 == 1) {
                            type3 = "岗位任务"
                        } else if (data.data[i].type3 == 2) {
                            type3 = "个人任务"
                        } else {
                            type3 = data.data[i].type3
                        }
                        this.task.push({
                            taskid: data.data[i].id,
                            id: i + (10 * this.pageNo),
                            title: data.data[i].title,
                            descs: data.data[i].descs,
                            attachments: data.data[i].attachments,
                            createdAt: new Date(data.data[i].createdAt).getFullYear() + "-" + (new Date(data.data[i].createdAt).getMonth() * 1 + 1) + "-" + new Date(data.data[i].createdAt).getDate(),
                            starttime: new Date(data.data[i].starttime).getFullYear() + "-" + (new Date(data.data[i].starttime).getMonth() * 1 + 1) + "-" + new Date(data.data[i].starttime).getDate(),
                            endtime: new Date(data.data[i].endtime).getFullYear() + "-" + (new Date(data.data[i].endtime).getMonth() * 1 + 1) + "-" + new Date(data.data[i].endtime).getDate(),
                            type3: type3,
                            finishTime: data.data[i].finishTime
                        })
                    }
                    console.log(this.task)
                }, err => {
                    console.log(err);
                    let alert = this.alertCtrl.create({
                        title: '服务器连接失败!'
                    });
                    alert.present();
                })

            loading.dismiss();
        })
    }

    goto(id) {
        this.navCtrl.push("TaskmorePage", { mess: this.task[id] })
    }
    show(){
        let alert = this.alertCtrl.create({
            title: '总体完成要求!'
        });
        alert.present();
    }
}



